.user-avatar {
  background-color: #8ac7ba;
  display: inline-block;
  width: 40px;
  height: 40px;
  line-height: 40px;
  border-radius: 50%; }

.user-avatar--default {
  background-color: #8ac7ba;
  text-align: center;
  color: #fff;
  font-size: 16px;
  font-weight: 400;
  text-transform: uppercase; }

.user-avatar--photo {
  background-position: center;
  background-size: cover; }

.user-avatar .user-avatar__icon {
  font-size: 21px;
  line-height: 40px;
  color: #fff; }



