.jobs-links {
  margin-top: 80px; }

.jobs-links__list {
  -webkit-columns: 2;
          columns: 2;
  list-style: none;
  margin-left: 0; }

.jobs-links__proposed-links {
  margin-left: 8px; }
  .jobs-links__proposed-links i {
    margin-left: 8px; }

.jobs-links__selected-links,
.jobs-links__remaining-links {
  margin-top: 40px; }

.jobs-links__jobs-item {
  color: #2f3233;
  font-size: 14px;
  font-weight: 500;
  line-height: 17px;
  margin-bottom: 18px;
  cursor: pointer; }



