.labeled-checkbox {
  margin: 24px 0; }

.labeled-checkbox__wrapper {
  width: 100%;
  border: 1px solid #dce1e4;
  border-radius: 4px;
  font-size: 14px;
  background-color: #fff;
  padding: 0 16px; }

.labeled-checkbox--error .labeled-checkbox__wrapper {
  border: 1px solid #f5456e; }



