.select-radio__option .form-radio--label-text {
  margin: 0;
  font-size: 14px;
  font-weight: 600;
  line-height: 17px;
  color: #2f3233; }

.select-radio__option .form-radio--description {
  margin-top: 6px;
  font-size: 14px;
  line-height: 24px;
  color: #818385; }

.select-radio__option .form-radio {
  display: inline-block;
  padding: 0 0 0 26px; }

.select-radio .Select-menu {
  max-height: unset; }

.select-radio .Select-menu-outer {
  min-width: 250px;
  position: absolute;
  right: 0;
  left: auto;
  max-height: unset;
  height: auto; }

.select-radio .form__group--radio {
  padding: 0; }

.select-radio .Select-option {
  padding: 20px 28px 17px 16px; }
  .select-radio .Select-option:not(:last-of-type) {
    border-bottom: 1px solid #d7dde0; }

.select-radio .Select-option.is-selected::before {
  display: none; }



