.tippy-tooltip {
  background-color: #55595c;
  border-radius: 0;
  padding: 16px 12px;
  font-size: 13px;
  max-width: 182px; }

.tippy-popper[x-placement^=top] [x-arrow] {
  border-top: 7px solid #55595c; }

.tippy-popper[x-placement^=bottom] [x-arrow] {
  border-bottom: 7px solid #55595c; }

.tippy-popper[x-placement^=left] [x-arrow] {
  border-left: 7px solid #55595c; }

.tippy-popper[x-placement^=right] [x-arrow] {
  border-right: 7px solid #55595c; }

.tooltip__link {
  color: #fff;
  font-weight: 700;
  cursor: pointer; }

.tooltip-container-margin {
  margin: 0 4px; }

.button--notAllowed {
  cursor: not-allowed;
  pointer-events: all !important; }



