.error-boundary__container {
  display: block !important; }
  @media (max-width: 480px) {
    .error-boundary__container {
      margin-bottom: 50px; } }

.error-boundary__container-table {
  display: table;
  width: 100%;
  height: calc(100vh - 626px); }
  @media (max-width: 480px) {
    .error-boundary__container-table {
      height: calc(100vh - 699px); } }
  @media (min-width: 481px) and (max-width: 768px) {
    .error-boundary__container-table {
      height: calc(100vh - 962px); } }

.error-boundary__table-cell {
  display: table-cell;
  vertical-align: middle;
  padding-top: 80px;
  padding-left: 16px;
  padding-right: 16px;
  text-align: center; }

.error-boundary__header {
  font-size: 48px;
  line-height: 56px;
  margin-bottom: 24px; }

.error-boundary__description {
  font-size: 18px;
  font-weight: 400;
  line-height: 30px; }



